import { useEffect, useState } from 'react';
import WeatherBox from './component/WeatherBox';
import WeatherButton from './component/WeatherButton';
import 'bootstrap/dist/css/bootstrap.min.css';
import ClipLoader from 'react-spinners/ClipLoader';
import './App.css';

//1.         
//2.    / ,    
//3.    5 (,   4)
//4.         
//5.         
//6.      
function App() {
    const [weather, setWeather] = useState(null);
    const [error, setError] = useState('');
    const [city, setCity] = useState('');
    const [id, setId] = useState('');
    const [icon, setIcon] = useState('');
    const [loading, setLoading] = useState(false); // false,  fetch  true   
    const cities = ['seoul', 'busan', 'jeju', 'new york', 'tokyo'];
    const [query, setQuery] = useState('');
    const API_KEY = "81523c3aa0ea13bf7e0d71967cd5d5d4";
    /*      */
    const getCurrentLocation = () => {
        navigator.geolocation.getCurrentPosition((position) => {
            let lat = position.coords.latitude;
            let lon = position.coords.longitude;
            //console.log('  ?', lat, lon);
            getWeatherByCurrentLocation(lat, lon);
        });
    };
    /*   */
    const search = async (e) => {
        try{
            if(e.key === "Enter"){
                fetch(`https://api.openweathermap.org/data/2.5/weather?q=${query}&appid=${API_KEY}&units=metric`)
                .then(res => res.json())
                .then(result => {
                    setWeather(result);
                    setQuery('');
                    //console.log("result ", result);
                })
            }
        }catch(error){
            console.log(' ?', error.message);
            errorRender(error.message);
        }
    }
    
    const errorRender = (message) => {
        setError(message);
        /*     ??? */
    }

    /*     */
    const getWeatherByCurrentLocation = async (lat, lon) => {
        let url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${API_KEY}&units=metric`;
        let response = await fetch(url);
        let data = await response.json();
        //console.log(' !', data);
        setWeather(data);
        setId(data.weather[0].id);
        setIcon(data.weather[0].icon);
        setLoading(false); // 
    };
    /*    */
    const getWeatherByCity = async () => {
        try {
            /* city state        */
            let url = `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${API_KEY}&units=metric`;
            let response = await fetch(url);
            let data = await response.json();
            //console.log('   ', data);
            setWeather(data);
            setId(data.weather[0].id);
            setIcon(data.weather[0].icon);
        } catch (error) {
            console.log(' ?', error.message);
            setError(error.message);
            setLoading(true);
        }
        setLoading(false); // 
    };

    useEffect(() => {
        /* useEffect componentDidUpdate   */
        /* useEffect     */
        if (city === '') {
            setLoading(true);
            getCurrentLocation();
        } else {
            setLoading(true);
            getWeatherByCity();
        }
    }, [city]);

    /*   */
    const handleCityChange = (city) => {
        if (city === 'current') {
            setCity('');
        } else {
            setCity(city);
        }
    };
    return (
        /* UI   */
        <div>
            {/*  true   */}
            {loading ? (
                <div className="container">
                    <ClipLoader color="#ff0000" loading={loading} size={100} />
                </div>
            ) : (
                <div className={(typeof weather.main != "undefined")
                ? ((weather.main.temp > 26)
                    ? 'container_warm' : 'container') : 'container'}>
                    <div className='search_box'>
                        <input
                        type="text"
                        placeholder='Search...'
                        onChange={e=>setQuery(e.target.value)}
                        value={query}
                        onKeyPress={search}
                        >
                        </input>
                    </div>
                    <WeatherBox weather={weather} id={id} icon={icon}/> {/* props  */}
                    <WeatherButton cities={cities} selectCity={city} handleCityChange={handleCityChange} /> {/* setCity  props  */}
                </div>
            )}
        </div>
    );
}

export default App;
